import RiveCanvas, { File, Artboard, StateMachineInstance, LinearAnimationInstance, SMIInput, FileAsset, FileAssetLoader, FontAsset } from "@rive-app/canvas-advanced";
import { Rive, decodeFont } from "@rive-app/canvas";
// import "style.css";

interface FloorInstanceData {
  x: number;
  y: number;
  row: number;
  col: number;
  artboard: Artboard;
  machine: StateMachineInstance;
  state: SMIInput;
}


var correctSVG = document.getElementById('correct');
var incorrectSVG = document.getElementById('incorrect');

window.gamestart = async function main() {
  const rive = await RiveCanvas({
    // Loads Wasm bundle
    locateFile: (_) =>
      `https://unpkg.com/@rive-app/canvas-advanced@2.7.6/rive.wasm`,
  });
  const canvas = document.getElementById("rive-canvas") as HTMLCanvasElement;
  canvas.height = document.documentElement.clientHeight;
  canvas.width = document.documentElement.clientWidth;

  const gradientDiv = document.getElementById("gradient");

  const scaleX = canvas.width / 1792;
  const scaleY = canvas.height / 922;

  const ROWS = 4;
  const COLS = 9;

  // const bgWidth = canvas.width - (padX + dim) * 2;
  // const bgWidth = canvas.width;
  
  // const fontbytes = await (
  //   await fetch(new Request("Montserrat-594893.ttf"))
  // ).arrayBuffer();
  
  // const font = (await rive.load(new Uint8Array(fontbytes))) as FontAsset;
  

  const renderer = rive.makeRenderer(canvas);

  var floorDim = 180 * scaleY;
  var totalDanceFloorWidth = floorDim * COLS;
  var totalDanceFloorHeight = floorDim * ROWS;
  var danceFloorPos = {
    x: (canvas.width - totalDanceFloorWidth) / 2,
    y: (canvas.height - totalDanceFloorHeight) / 2 + 50 * scaleY
  }

  if (gradientDiv) {
    gradientDiv.style.width = totalDanceFloorWidth + 'px';
    gradientDiv.style.height = totalDanceFloorHeight + 'px';
    gradientDiv.style.left = danceFloorPos.x + 'px';
    gradientDiv.style.top = danceFloorPos.y + 'px';
  }
  

  const floorBytes = await (
    // await fetch(new Request("2023-10-23-tile_test-001.riv"))
    await fetch(new Request("DanceFloor.riv"))
    // await fetch(new Request("Button.riv"))
  ).arrayBuffer();
  const floorFile = (await rive.load(new Uint8Array(floorBytes))) as File;
  const artboardFloor = floorFile.defaultArtboard();

  // const floorMachine = new rive.StateMachineInstance(
  //   artboardFloor.stateMachineByName("State Machine"),
  //   artboardFloor
  // );
  
  const timerBytes = await (
    await fetch(new Request("Timer.riv"))
  ).arrayBuffer();
  const timerFile = (await rive.load(new Uint8Array(timerBytes))) as File;
  const artboardTimer = timerFile.defaultArtboard();

  const timerInfo = { x: 0, y: 0, w: 0, h: 0};

  timerInfo.w = 250 * scaleX;
  timerInfo.h = 100 * scaleX;
  timerInfo.x = canvas.width / 2 - timerInfo.w / 2;
  timerInfo.y = 10 * scaleY;

  const timerMachine = new rive.StateMachineInstance(
    artboardTimer.stateMachineByName("State Machine"),
    artboardTimer
  );

  const gameDuration = 60;
  var timerT = gameDuration;
  var timerMachineProgress;
  var timerMachineUrgent;

  for (let i = 0, l = timerMachine.inputCount(); i < l; i++) {
    const input = timerMachine.input(i);
    // console.log(input.name, input.type)
    // console.log(input, input.name, input.type, input.asNumber().value)
    if (input.name == "Timer") {
      timerMachineProgress = input;
    } else if (input.name == "Urgent") {
      timerMachineUrgent = input;
    } 
  }

  let lastTime = 0;
  const floor = floorFile.defaultArtboard();
  const floors = new Set<FloorInstanceData>();
  const danceFloorGrid = [] as any;
  const crawlGrid = [] as any;
  const crawlStack = [] as any;
  // x, y
  const DIRECTIONS = [
    [0, -1], // UP
    [1, 0], // RIGHT
    [0, 1], // DOWN
    [-1, 0], // LEFT
  ];

  var difficulty = 4;
  var maxDifficulty = 12;
  var currLevel = 0;
  var nextLevel = 3;
  var crawlT = 0;
  var crawlT2 = 0;
  var crawlT3 = 0;
  var crawlT4 = 0;
  var crawlCount = 0;
  var isCorrect = false;
  

  generateDanceFloor();
  crawl();

  window.addEventListener('resize', (e) => {
    location.href = '';
  });

  let mDown = false;


  canvas.addEventListener('mousedown', (e) => {
    let mx = e.offsetX;
    let my = e.offsetY;

    if (!mDown) {
      mDown = true;

      let row = Math.floor((my - danceFloorPos.y) / floorDim);
      let col = Math.floor((mx - danceFloorPos.x) / floorDim);
      
      if (crawlT3 == 0 && crawlT4 == 0) {
        if (row > -1 && col > -1 && row < ROWS && col < COLS) {
          if (crawlGrid[row][col]) {
            if (danceFloorGrid[row][col] < 2) {
              danceFloorGrid[row][col] = 2;
              crawlCount++;
              if (crawlCount == difficulty) {
                crawlT4 = 5;
                isCorrect = true;
                if (correctSVG) {
                  correctSVG.classList.remove("show");
                  correctSVG.classList.remove("hide");
                  correctSVG.classList.add("show");
                }
                
              }
            }
          } else {
            crawlT4 = 5;
            isCorrect = false;
            if (incorrectSVG) {
              incorrectSVG.classList.remove("show");
              incorrectSVG.classList.remove("hide");
              incorrectSVG.classList.add("show");
            }
          }
          
        }
      }
    }
  });

  canvas.addEventListener('mouseup', (e) => {
    if (mDown) {
      mDown = false;
    }
  });

  function generateDanceFloor() {
    for (let i = 0; i < ROWS; ++i) {
      danceFloorGrid[i] = [];
      crawlGrid[i] = [];
      for (let j = 0; j < COLS; ++j) {
        danceFloorGrid[i][j] = 0;
        crawlGrid[i][j] = 0;
        // const floorInstance = floorFile.artboardByName("artboard");
        const floorInstance = floorFile.defaultArtboard();
        const floorMachine = new rive.StateMachineInstance(
          floor.stateMachineByName("State Machine"),
          floorInstance
        );

        let state;

        for (let k = 0; k < floorMachine.inputCount(); k++) {
          const input = floorMachine.input(k);
          switch (input.name) {
            case "State":
              state = input;
              break;
  
            default:
              break;
          }
        }

        const blockInstance = {
          x: danceFloorPos.x + j * floorDim,
          y: danceFloorPos.y + i * floorDim,
          row: i,
          col: j,
          artboard: floorInstance,
          machine: floorMachine,
          state: state
        };
  
        floorMachine.advance(0);
        blockInstance.artboard.advance(0);
  
        floors.add(blockInstance);
      }
    }
  }

  function shuffleArr(array) {
    for (var i = array.length - 1; i > 0; i--) {
        var rand = Math.floor(Math.random() * (i + 1));
        [array[i], array[rand]] = [array[rand], array[i]]
    }
  }

  function lerp(a, b, t) {
    return a + (b - a) * t;
  }

  function generateRNG(n) {
    return Math.floor(Math.random() * n);
  }

  function clearGrid(grid, v) {
    for (let i = 0; i < grid.length; ++i) {
      for (let j = 0; j < grid[i].length; ++j) {
        grid[i][j] = v;
      }
    }
  }

  function crawl() {
    let x = generateRNG(COLS);
    let y = generateRNG(ROWS);

    if (isCorrect) {
      currLevel++;
      if (currLevel == nextLevel) {
        difficulty = Math.min(difficulty + 1, maxDifficulty);
        currLevel = 0;
      }
    }
    
    while (crawlStack.length) {
      let c = crawlStack.pop();
      crawlGrid[c[1]][c[0]] = 0;
    }

    // console.log(crawlStack, crawlGrid);

    // clearGrid(crawlGrid, 0);

    crawlGrid[y][x] = 1;
    crawlStack.push([x, y]);

    let idx = 0;

    for (let i = 1; i < difficulty; ++i) {
      let flag = true;

      while (flag && idx >= 0) {
        shuffleArr(DIRECTIONS);
        for (let j = 0; j < DIRECTIONS.length; ++j) {
          let d = DIRECTIONS[j];
  
          let r = y + d[1];
          let c = x + d[0];
          // console.log(r, c);
  
          if (r >= 0 && r < ROWS && c >= 0 && c < COLS) {
            if (crawlGrid[r][c] == 0) {
              crawlGrid[r][c] = 1;
              crawlStack.push([c, r]);
              idx = crawlStack.length - 1;
              x = c;
              y = r;
              flag = false;
              break;
            }
          }
        }

        if (flag) {
          if (--idx >= 0) {
            x = crawlStack[idx];
            y = crawlStack[idx];
          }
        }
      }
    }
    // console.log(crawlStack);
    crawlT = crawlStack.length + 15;
    crawlT2 = 0;
    crawlT3 = 10;
    crawlCount = 0;
    crawlT4 = 0;

    isCorrect = false;
  }

  function animateCrawl(delta) {
    if (crawlT > 0) {
      
      let idx = Math.min(Math.floor(crawlT2), crawlStack.length - 1);
      
      let x = crawlStack[idx][0];
      let y = crawlStack[idx][1];

      danceFloorGrid[y][x] = 1;

      crawlT -= 10 * delta;
      crawlT2 += 10 * delta;
      
      if (crawlT < 0) {
        crawlT = 0;
        clearGrid(danceFloorGrid, 0);
      }
    } else if (crawlT3 > 0) {
      crawlT3 -= 10 * delta;
      if (crawlT3 < 0) {
        crawlT3 = 0;
        clearGrid(danceFloorGrid, 1);
      }
    } else if (crawlT4 > 0) {
      crawlT4 -= 5 * delta;

      if (crawlT4 < 0) {
        crawlT4 = 0;
        if (correctSVG) {
          correctSVG.classList.remove("show");
          correctSVG.classList.remove("hide");
          correctSVG.classList.add("hide");
          
        }

        if (incorrectSVG) {
          incorrectSVG.classList.remove("show");
          incorrectSVG.classList.remove("hide");
          incorrectSVG.classList.add("hide");
        }

        clearGrid(danceFloorGrid, 0);
        crawl();
      }
    }
  }

  function renderLoop(time) {
    if (!lastTime) {
      lastTime = time;
    }
    const elapsedTimeMs = time - lastTime;
    const elapsedTimeSec = elapsedTimeMs / 1000;
    lastTime = time;

    renderer.clear();
    renderer.save();

    if (timerT > 0) {
      timerT -= elapsedTimeSec;

      if (timerT < 0) {
        timerT = 0;
        
        setTimeout(() => {
         
          timerT = gameDuration;
          timerMachineUrgent.asBool().value = false;

        }, 1000)
      }
      let currT = Math.floor(timerT);

      if (!timerMachineUrgent.asBool().value && currT < 11) {
        timerMachineUrgent.asBool().value = true;
      }

      let percent = timerT / gameDuration * 100;
      timerMachineProgress.asNumber().value = percent;
      // console.log(timerMachineProgress.asNumber().value)
      var textRun = artboardTimer.textRun("TimeLeft");
      textRun.text = currT.toString();
    }

    timerMachine.advance(elapsedTimeSec);

    animateCrawl(elapsedTimeSec);

    for (let blockInstance of Array.from(floors)) {
      const { artboard, machine, x, y, state, row, col } = blockInstance;
      
      renderer.save();

      renderer.translate(x, y);
      renderer.align(
        rive.Fit.contain,
        rive.Alignment.center,
        {
          minX: 0,
          minY: 0,
          maxX: floorDim,
          maxY: floorDim
        },
        artboard.bounds
      );

      // tapTrigger.fire();

      state.asNumber().value = danceFloorGrid[row][col];
      
      machine.advance(elapsedTimeSec);
      artboard.advance(elapsedTimeSec);
      artboard.draw(renderer);
      renderer.restore();
    }

    // timer
    renderer.save();
    renderer.translate(timerInfo.x, timerInfo.y);
    renderer.align(
      rive.Fit.contain,
      rive.Alignment.center,
      {
        minX: 0,
        minY: 0,
        maxX: timerInfo.w,
        maxY: timerInfo.h
      },
      artboardTimer.bounds
    );

    artboardTimer.advance(elapsedTimeSec);
    artboardTimer.draw(renderer);

    renderer.restore();

    
    rive.requestAnimationFrame(renderLoop);
  }
  rive.requestAnimationFrame(renderLoop);
}

